.container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.dataGrid {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 80%;
    flex-grow: 1;
    padding: 1rem 0 1.5rem;
    width: 100%;
}

.textEditor {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    flex-grow: 1;
    padding: 1rem 0 1.5rem;

    div {
        flex-grow: 1;
    }
}

.editForm {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 80%;
    overflow: auto;
    padding: 0 2rem 1.5rem;
}
    
.content {
    height: 90%;
}

.menuList {
    width: 250px
}

.menuItem__nested {
    padding-left: 1rem
}

.idLabel {
    margin: 0;
}

.inactive {
    background-color: rgba(0, 0, 0, .15);
    color: rgba(0, 0, 0, .3);
}